import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../../components/Layout';
import Container from '../../components/Container';
import SpaceDivider from '../../components/SpaceDivider';
import Footer from '../../components/Footer';

import Block4Part from '../../components/Block4Part';
import BlockCTA from '../../components/BlockCTA';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockLogos from '../../components/BlockLogos';
import BlockQuote from '../../components/BlockQuote';

import GrowthIcon from '../../components/icons/Growth';
import MoneyIcon from '../../components/icons/Money';
import ShieldIcon from '../../components/icons/Shield';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    simpleTire: file(name: { eq: "simple-tire" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    OndemandTiresImage: contentfulCompany(name: { eq: "Ondemand tires" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    LEDMyplaceImage: contentfulCompany(name: { eq: "LEDMyplace" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    ikioImage: contentfulCompany(name: { eq: "ikio" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    supportGrowthImage: file(name: { eq: "support-growth" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    repeatCustomersImage: file(name: { eq: "repeat-customers" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    commonLivingSmall: file(name: { eq: "common-living-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    homeFurnishingsSmall: file(name: { eq: "home-furnishings-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fixed(width: 150) {
              ...GatsbyContentfulFixed
            }
          }
        }
      }
    }
  }
`;

export default function SEMLandingPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout landingNav>
      <GatsbySeo title="Offer Net Terms Without Risk" />
      <Container className="lp">
        <BlockHeaderNoImage
          title={
            <span>
              Have a Magento account and want to offer payment flexibility to
              your B2B customers?
            </span>
          }
          desc="Resolve can help you with that, our net 30/60/90 day payment terms offer the flexibility your B2B clients seek while making sure you get paid sooner."
          primaryButtonText="Get A Demo"
          primaryButtonTo="/contact-sales/"
        />

        <SpaceDivider height={50} />

        <BlockLogos
          pretitle="Trusted by the world's most innovative companies"
          data={[
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 15,
            },
            {
              image: companies['ConEquip'],
              name: 'ConEquip',
              offsetTop: 10,
            },
            {
              image: companies['Jupiter Pro Parts'],
              name: 'Jupiter Pro Parts',
              offsetTop: -10,
            },
            {
              image: companies['Ozark LED'],
              name: 'Ozark LED',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 10,
            },
            {
              image: companies['Go Materials'],
              name: 'Go Materials',
              offsetTop: -10,
            },
          ]}
        />

        <SpaceDivider height={50} />

        <Block4Part
          title="Take away the stress of trying to understand how to offer net payment terms to your B2B customers"
          desc="Resolve takes care of everything for you so that you can focus on growing your business"
          items={[
            {
              title: 'Increase Working Capital',
              content: (
                <div>
                  Receive your payments before the net payment terms offered to
                  your customers
                </div>
              ),
              icon: <GrowthIcon height="32" />,
            },
            {
              title: 'Automate Your Processes',
              content: (
                <div>
                  Hassle free, credit checks, trade reference checks are all
                  automated
                </div>
              ),
              icon: <MoneyIcon height="32" />,
            },
            {
              title: 'Reduce Your Risk',
              content: (
                <div>
                  With Resolve payments are guaranteed so that you can reduce
                  your accounts receivable
                </div>
              ),
              icon: <ShieldIcon width="32" height="32" />,
            },
          ]}
        />

        <SpaceDivider height={100} />

        <BlockQuote
          title="Account Executive at Hyperikon"
          author="Ray Gil"
          image={companies['Hyperikon']}
        >
          "Resolve has been like a spring... allowing us to grow and scale your
          B2B business more quickly than any other company. It's been a catalyst
          to faster and better growth for us."
        </BlockQuote>

        <SpaceDivider height={100} />

        <BlockCTA
          pretitle="Ready to get started?"
          title="Resolve helps B2B merchants simplify the way they offer net payment terms to their customers"
          primaryButtonText="Talk to us today"
          primaryButtonTo="/contact-sales/"
          align="center"
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .lp__section {
          text-align: center;
        }

        .lp__contact-us-button {
          display: block;
          margin: 0 auto;
        }

        .main-image {
          max-height: 300px;
        }

        .lp .app__actions button {
          margin-right: 0;
        }
      `}</style>
    </Layout>
  );
}
